export default {
  props: {
    x: Number,
    y: Number,
    fill: String,
    opacity: Number
  },
  methods: {
    hexDrawSceneFunc(context, shape) {
      context.beginPath();
      context.moveTo(0, 30);
      context.lineTo(-26, 15);
      context.lineTo(-26, -15);
      context.lineTo(0, -30);
      context.lineTo(26, -15);
      context.lineTo(26, 15);
      context.closePath();
      context.fillStrokeShape(shape);
    }
  }
};
